var Arr = {
  /**
   * 12
   * @param  {[type]} arr1 1
   * @param  {[type]} arr2 2
   * @returns 
   */
  difference(arr1, arr2) {
    try {
      let arr = [];
      let i = 0;
      let len1 = arr1.length;
      for (i = 0; i < len1; i++) {
        if (!arr2.contains(arr1[i])) {
          arr.push(arr1[i]);
        }
      }
      return arr;
    } catch (e) {
      return arr1;
    }
  },
  /**
   * 
   * @param  {[type]} arr 
   * @return {[type]}     [description]
   */
  upsetArr(arr) {
    return arr.sort(function() {
      return 0.5 - Math.random();
    });
  }
};

