var Cookie = {
  /**
   * cookie
   * @param name 
   * @param value 
   * @param expires  24h
   */
  set(name, value, expires) {
    const expTimes = expires ? Number(expires) * 60 * 60 * 1000 : 24 * 60 * 60 * 1000; // 
    const expDate = new Date();
    expDate.setTime(expDate.getTime() + expTimes);
    const expString = `; expires=${expDate.toUTCString()}`;
    const pathString = '; path=/';
    document.cookie = `${name}=${encodeURI(value)}${expString}${pathString}`;
  },
  /**
   * cookie
   * @param name
   */
  get(name) {
    const cookieStr = `; ${document.cookie}; `;
    const index = cookieStr.indexOf(`; ${name}=`);
    if (index !== -1) {
      let s = cookieStr.substring(index + name.length + 3, cookieStr.length);
      return decodeURI(s.substring(0, s.indexOf('; ')));
    } else {
      return null;
    }
  },
  /**
   * cookie
   * @param name
   */
  del(name) {
    const exp = new Date(new Date().getTime() - 1);
    const s = this.get(name);
    if (s !== null) {
      document.cookie = `${name}=${s}; expires=${exp.toUTCString()}; path=/`;
    }
  }
};

var DateTime = {
  /**
   * 
   * @param  {string} str yyyy-MM-dd HH:mm  yyyy/MM/dd HH:mm
   * iphone2016-02-26 09:122016/02/26 09:12
   * @return {number}     [description]
   */
  strToTimestamp: function(str) {
    return Date.parse(str.replace(/-/g, '/'));
  },
  strToDate: function(str) {
    return new Date(str.replace(/-/g, '/'));
  },
  /**
   * 
   * @param  {[type]} t 
   * @param  {[type]} splitStr 
   * @return {[type]}   [description]
   */
  timestampToDateStr: function(t, splitStr) {
    return this.dateToString(this.timestampToDate(t), splitStr);
  },

  /**
   * 
   * @param  {[type]} t 
   * @return {[type]}   
   */
  timestampToDate: function(t) {
    return new Date(t);
  },

  /**
   * 
   * @param  {[type]} d           
   * @param  {[type]} splitStr 
   * @return {[type]}              MM-dd HH:mm
   */
  dateToString(d, splitStr, withYear = false) {
    var year = d.getFullYear().toString(),
      month = (d.getMonth() + 1).toString(),
      day = d.getDate().toString(),
      h = d.getHours().toString(),
      m = d.getMinutes().toString();
    month = month.length > 1 ? month : `0${month}`;
    day = day.length > 1 ? day : `0${day}`;
    h = h.length > 1 ? h : `0${h}`;
    m = m.length > 1 ? m : `0${m}`;
    var str = `${(withYear ? `${year}-` : '') + month}-${day} ${h}:${m}`; // MM-dd HH:mm
    if (splitStr) {
      str = str.replace(/-/g, splitStr);
    }
    return str;
  },

  /**
   * ()
   * @param  {Date} d           
   * @param  {string} splitStr 
   * @return {string}              yyyy-MM-dd HH:mm
   */
  dateToStringWithYear: function(d, splitStr) {
    return this.dateToString(d, splitStr, true);
  },
  /**
   *  
   * xxxx HH:mm HH:mmMM-dd HH:mm
   * @param  {string} str 2016-02-26 09:12
   * @return {string}
   */
  getSpecialTimeStr: function(str) {
    const targetDate = this.strToDate(str);
    const targetTime = targetDate.getTime();
    const targetGetDate = targetDate.getDate();
    const currentDate = new Date();
    const currentTime = currentDate.getTime();
    const currentGetDate = currentDate.getDate();
    if (!targetTime) {
      return false;
    }

    const tdoa = Number(currentTime - targetTime),
      dayTime = 24 * 60 * 60 * 1000, // 1
      hourTime = 60 * 60 * 1000, // 1
      minuteTime = 60 * 1000; // 1

    // 
    const h = tdoa / dayTime;
    if (h < 2) {
      if (currentGetDate - targetGetDate === 1) {
        return '';
      } else if (currentGetDate - targetGetDate === 2) {
        return '';
      } else if (currentGetDate - targetGetDate === 0) {
        if (tdoa >= hourTime) {
          // 
          return `${Math.floor(tdoa / hourTime)}`;
        } else if (tdoa >= minuteTime) {
          return `${Math.floor(tdoa / minuteTime)}`;
        } else {
          return '';
          // return Math.floor(tdoa / 1000) + '';
        }
      }
    } else if (h >= 2 && h < 3 && currentGetDate - targetGetDate === 2) {
      return '';
    } else {
      return this.dateToString(targetDate);
    }
  },
  /**
   * 
   * @param  {Number}  seconds []
   * @param  {Boolean} hasHour []
   * @return {String}          hasHour[true]: hh:mm:ss[]mm:ss
   */
  secondsToTimestr(seconds, hasHour = false) {
    var hh, mm, ss;
    // 0
    if (seconds == null || seconds < 0) {
      return;
    }
    seconds = Math.ceil(seconds);
    // 
    hh = parseInt(seconds / 3600) | 0;
    seconds = parseInt(seconds) - hh * 3600;
    if (hh < 10) {
      hh = `0${hh}`;
    }
    // 
    mm = (seconds / 60) | 0;
    if (parseInt(mm) < 10) {
      mm = `0${mm}`;
    }
    // 
    ss = parseInt(seconds) - mm * 60;
    if (ss < 10) {
      ss = `0${ss}`;
    }
    if (hasHour) {
      return `${hh}:${mm}:${ss}`;
    }
    return `${mm}:${ss}`;
  },
  /**
   * 
   * @param  {Number}  seconds []
   * @param  {Boolean} hasHour []
   * @return {String}          hasHour[true]: hh:mm:ss[]mm:ss
   */
  timestampToTimestr: function(ts, hasHour) {
    var seconds = ts ? Number(ts) / 1000 : 0;
    seconds = (parseInt(seconds) % (60 * 60 * 24)) + 8 * 60 * 60; //
    return this.secondsToTimestr(seconds, hasHour);
  }
};

var Html = {
  /**
   * js
   * @param  {string}   url      jsurl
   * @param  {Function} callback 
   */
  getScript(url, callback, element) {
    var head = document.getElementsByTagName('head')[0],
      js = document.createElement('script');

    js.setAttribute('type', 'text/javascript');
    js.setAttribute('src', url);
    if (element) {
      element.appendChild(js);
    } else {
      head.appendChild(js);
    }
    //
    var callbackFn = function() {
      if (typeof callback === 'function') {
        callback();
      }
    };

    if (document.all) {
      // IE
      js.onreadystatechange = function() {
        if (js.readyState === 'loaded' || js.readyState === 'complete') {
          callbackFn();
        }
      };
    } else {
      js.onload = function() {
        callbackFn();
      };
    }
  },
  /**
   * 
   * @param  {string}   scriptCode     
   * @param  {Function} callback   
   * @param  {DOM}   element  js
   * @return {undefined}
   */
  createScript(scriptCode, callback, element) {
    if (scriptCode) {
      var head = document.getElementsByTagName('head')[0],
        js = document.createElement('script');
      js.setAttribute('type', 'text/javascript');
      js.innerHTML = scriptCode;
      if (element) {
        element.appendChild(js);
      } else {
        head.appendChild(js);
      }
      //
      callback();
    }
  },
  /**
   * css
   * @param  {String} style css
   * @param {Function} callback css
   * @param {DOM} element DOM
   */
  createStyle(style, callback, element) {
    if (style) {
      var head = document.getElementsByTagName('head')[0],
        css = document.createElement('style');
      css.innerHTML = style;
      if (element) {
        element.appendChild(css);
      } else {
        head.appendChild(css);
      }
      //
      callback && callback();
    }
  },
  /**
   * html
   * @param  {String} str 
   * @return {String}     
   */
  filterHtmlTags(str) {
    if (!str || typeof str !== 'string') {
      return '';
    }
    return str.replace(/<\/?[^>]*>/g, '');
  },
  /**
   * 
   * @return {[type]} [description]
   */
  getScrollTop() {
    var scrollTop = 0,
      bodyScrollTop = 0,
      documentScrollTop = 0;
    try {
      if (document.body) {
        bodyScrollTop = document.body.scrollTop;
      }
      if (document.documentElement) {
        documentScrollTop = document.documentElement.scrollTop;
      }
    } catch (e) {
      console.error(e);
    }
    scrollTop = bodyScrollTop - documentScrollTop > 0 ? bodyScrollTop : documentScrollTop;
    return scrollTop;
  }
};

var Num = {
  /**
   * xxxxxx, 10.210.9  11
   * @param  {String|Number} num 
   * @return {String}    
   */
  getSpecialCountStr(num) {
    if (typeof num !== 'string' && typeof num !== 'number') {
      return num;
    }
    num = parseInt(num, 10);
    if (num > 9999) {
      return `${Math.ceil(num / 10000)}`;
    }
    return `${num}`;
  }
};

